* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::before,
*::after {
  box-sizing: border-box;
}

li {
  list-style: none;
}

.page {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
}

.page > div {
  width: 50vw;
  height: 50vh;
  border: 1px solid deeppink;
}
